.grid-item {
  position: relative;
  min-height: 1px;
  padding-left: 0;
  padding-right: 0;
  flex: 0 0 100%; }
  .grid-item a {
    display: block; }
    .grid-item a .dim {
      display: block;
      position: absolute;
      background: #ffffff;
      opacity: 0.3;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      backface-visibility: hidden;
      transition: opacity 500ms ease; }
    .grid-item a .title {
      display: block;
      position: absolute;
      background: #000000;
      opacity: 0.2;
      right: 0;
      bottom: 0;
      left: 0;
      margin: 5px;
      border-radius: 0;
      text-align: center;
      white-space: normal;
      transition: opacity 500ms ease; }
    .grid-item a:hover .dim,
    .grid-item a:focus .dim {
      opacity: 0; }
    .grid-item a:hover .title,
    .grid-item a:focus .title {
      opacity: 0.5; }
    @media (max-width: 767px) {
      .grid-item a .dim {
        opacity: 0; }
      .grid-item a .title {
        opacity: 0.5; } }
