  @media (min-width: 768px) {
    .grid-item {
      flex: 0 0 33.33333%; } }
  @media (min-width: 992px) {
    .grid-item {
      flex: 0 0 25%; } }
  @media (min-width: 1200px) {
    .grid-item {
      flex: 0 0 16.66667%; } }

